import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { UserService } from '../shared/services/user.service';
import { AuthService } from '../shared/services/auth/auth.service';
import { Router } from '@angular/router';

@Component({
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

    public loginForm: FormGroup;

    constructor(private authService: AuthService, private uService: UserService, private router: Router) {
        this.router.navigate(['/mobile/home']);
        // if (this.authService.getStoredUser()) {
        //     alert('Navigate');
        // }
    }

    ngOnInit(): void {
        this.loginForm = new FormGroup({
            username: new FormControl('c2', [Validators.required]),
            password: new FormControl('123456', [Validators.required]),
        });
    }

    login() {
        if (this.loginForm.valid) {
            this.uService.login(this.loginForm.value).subscribe(resp => {
                if (resp.status) {
                    this.router.navigate(['/mobile/terms-conditions']);
                }
            });
        }
    }
}
