import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GlobalService } from '../../shared/services/global.service';
import { UserService } from '../../shared/services/user.service';

@Component({
    selector: 'mobile-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

    public menuOpen = false;
    public balance;
    public commentary;

    constructor(private router: Router, private gService: GlobalService, private uService: UserService) { }

    navigateTo(name) {
        this.router.navigate(['mobile/' + name]);
    }

    ngOnInit(): void {
        this.gService.balance$.subscribe(data => {
            this.balance = data;
        })

        this.gService.commentary$.subscribe(data => {
            this.commentary = data;
        })
    }

    logout() {
        this.uService.logout();
    }
}
