import { Component, OnInit } from '@angular/core';
import { SportsService } from '../../shared/services/sports.service';
import { MarketService } from '../../shared/services/market.service';

@Component({
  selector: 'app-market',
  templateUrl: './market.component.html',
  styleUrls: ['./market.component.css'],
  providers: [SportsService, MarketService]
})
export class MarketComponent implements OnInit {

  sports = [];
  marketList = {};

  constructor(private sService: SportsService, private mService: MarketService) { }

  ngOnInit() {
    this.sService.getSports().subscribe(resp => {
      if (resp.status) {
        this.sports = resp.data.items;
        this.sports.forEach((sport, index) => {
          this.marketList[sport.event_slug] = [];
          this.getMarketList(sport.event_slug, index);
        });
      }
    })
  }

  getMarketList(slug, index) {
    this.mService.getMarketList(slug).subscribe(resp => {
      // console.log('----------- Market List ----------', resp);
      if (resp.status && resp.data.items) {
        this.marketList[slug] = resp.data.items;
      }
      console.log('----------- Market List ----------', this.marketList);
    });
  }

}
