import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CommonModule } from '@angular/common';
import { MobileComponent } from './mobile.component';
import { LoginComponent } from './login/login.component';
import { MainComponent } from './layout/main/main.component';
import { HomeComponent } from './inner-pages/home/home.component';
import { AccountStatementComponent } from './inner-pages/account-statement/account-statement.component';
import { BetHistoryComponent } from './inner-pages/bet-history/bet-history.component';
import { BetStakeSettingComponent } from './inner-pages/bet-stake-setting/bet-stake-setting.component';
import { FavoriteComponent } from './inner-pages/favorite/favorite.component';
import { InPlayComponent } from './inner-pages/in-play/in-play.component';
import { SportsComponent } from './inner-pages/sports/sports.component';
import { ScoresComponent } from './inner-pages/scores/scores.component';
import { MarketComponent } from './inner-pages/market/market.component';
import { ChangePasswordComponent } from './inner-pages/change-password/change-password.component';
import { ProfitLossComponent } from './inner-pages/profit-loss/profit-loss.component';
import { ResultsComponent } from './inner-pages/results/results.component';
import { AuthGuard } from './shared/services/auth/auth.guard';
import { MyProfileComponent } from './inner-pages/my-profile/my-profile.component';
import { TermsComponent } from './terms/terms.component';

const routes: Routes = [
    {
        path: '', component: MobileComponent, children: [
            { path: 'login', component: LoginComponent },
            { path: 'terms-conditions', component: TermsComponent },
            {
                path: '', component: MainComponent, canActivate: [AuthGuard], children: [
                    { path: '', component: HomeComponent },
                    { path: 'home', component: HomeComponent },
                    { path: 'my-profile', component: MyProfileComponent },
                    { path: 'account-statement', component: AccountStatementComponent },
                    { path: 'bet-history', component: BetHistoryComponent },
                    { path: 'bet-stake-setting', component: BetStakeSettingComponent },
                    { path: 'favorite', component: FavoriteComponent },
                    { path: 'in-play', component: InPlayComponent },
                    { path: 'market', component: MarketComponent },
                    { path: 'profit-loss', component: ProfitLossComponent },
                    { path: 'results', component: ResultsComponent },
                    { path: 'scores', component: ScoresComponent },
                    { path: 'sports', component: SportsComponent },
                    { path: 'sports/:slug', component: SportsComponent },
                    { path: 'change-password', component: ChangePasswordComponent },
                ]
            }
        ]
    },
];

@NgModule({
    imports: [CommonModule, RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class MobileRoutingModule { }
