import { Component, OnInit } from '@angular/core';
import { InPLayService } from '../../shared/services/inplay.service';
import { UserService } from '../../shared/services/user.service';

@Component({
  selector: 'app-in-play',
  templateUrl: './in-play.component.html',
  styleUrls: ['./in-play.component.css'],
  providers: [InPLayService]
})
export class InPlayComponent implements OnInit {

  public inplay;
  public today;
  public tomorrow;
  public activeTab = 'inplay';


  constructor(private ipService: InPLayService, private uService: UserService) { }

  ngOnInit() {
    this.ipService.getMatchList().subscribe(resp => {
      console.log(resp);
      if (resp.status) {
        this.inplay = resp.data.items.inplay;
        this.today = resp.data.items.today;
        this.tomorrow = resp.data.items.tomorrow;
      }
    })
  }

  setTab(tabName) {
    this.activeTab = tabName;
  }

  addRemoveFavorite(item, index) {
    const data = item.list[index];
    const requestData = {
      "event_id": data.eventId,
      "market_id": data.marketId,
      "market_type": "match_odd",
      "favorite": +data.is_favorite ? 'remove' : 'add'
    };
    this.uService.addRemoveFavorite(requestData).subscribe(resp => {
      if (resp.status) {
        data.is_favorite = !+data.is_favorite;
      }
    });
  }
}
