import { Injectable } from '@angular/core';
import { CustomHttpClient } from './api/custom-http-client';

@Injectable()
export class FavoriteService {
    constructor(private chhtp: CustomHttpClient) { }

    getFavoriteList(slug) {
        return this.chhtp.post(`v2/events/app-event/my-favorite-${slug}`, {});
    }
}