import { Component, OnInit } from '@angular/core';
import { SportsService } from '../../shared/services/sports.service';
import { UserService } from '../../shared/services/user.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-sports',
  templateUrl: './sports.component.html',
  styleUrls: ['./sports.component.css'],
  providers: [SportsService]
})
export class SportsComponent implements OnInit {

  sports = [];
  events = [];
  selected = 0;

  constructor(private sService: SportsService, private uService: UserService, private aRoute: ActivatedRoute) { }

  ngOnInit() {

    const slug = this.aRoute.snapshot.params.slug;

    this.sService.getSports().subscribe(resp => {
      if (resp.status) {
        this.sports = resp.data.items;
        this.sports.forEach((sport, index) => {
          if (!this.selected) {
            this.selected = (sport.event_slug === slug) ? index : 0;
          }
          this.events[index] = {};
          this.getSportsEvents(sport.event_slug, index);
        });
      }
    })
  }

  getSportsEvents(slug, index) {
    this.sService.getSportsEvents(slug).subscribe(resp => {
      if (resp.status) {
        this.events[index] = resp.data.items;
      }
    });
  }

  addRemoveFavorite(item, index) {
    const data = item[index];
    const requestData = {
      "event_id": data.eventId,
      "market_id": data.marketId,
      "market_type": "match_odd",
      "favorite": +data.is_favorite ? 'remove' : 'add'
    };
    this.uService.addRemoveFavorite(requestData).subscribe(resp => {
      if (resp.status) {
        data.is_favorite = !+data.is_favorite;
      }
    });
  }
}
