import { Component, OnInit } from '@angular/core';
import { FavoriteService } from '../../shared/services/favorite.service';
import { SportsService } from '../../shared/services/sports.service';

@Component({
  selector: 'app-favorite',
  templateUrl: './favorite.component.html',
  styleUrls: ['./favorite.component.css'],
  providers: [SportsService, FavoriteService]
})
export class FavoriteComponent implements OnInit {

  sports = [];
  events = [];

  constructor(private sService: SportsService, private fService: FavoriteService) { }

  ngOnInit() {
    this.sService.getSports().subscribe(resp => {
      if (resp.status) {
        this.sports = resp.data.items;
        this.sports.forEach((sport, index) => {
          this.events[index] = {};
          this.getFavoriteList(sport.event_slug, index);
        });
      }
    })
  }

  getFavoriteList(slug, index) {
    this.fService.getFavoriteList(slug).subscribe(resp => {
      console.log('-------- getFavoriteList --------', resp);
      if (resp.status && resp.data.items) {
        this.events[index] = resp.data.items;
      }
    });
  }


}
