import { Component, OnInit } from '@angular/core';
import { UserService } from '../../shared/services/user.service';
import { NgxSmartModalService } from 'ngx-smart-modal';

@Component({
  selector: 'app-profit-loss',
  templateUrl: './profit-loss.component.html',
  styleUrls: ['./profit-loss.component.css']
})
export class ProfitLossComponent implements OnInit {

  profitLoss = [];
  selectedBet;

  constructor(private uService: UserService, public ngxSmartModalService: NgxSmartModalService) { }

  ngOnInit() {
    this.uService.getProfitLoss().subscribe(resp => {
      console.log(resp);
      if (resp.status) {
        this.profitLoss = resp.data.items;
      }
    });
  }

  openBetIndex(index) {
    this.selectedBet = this.profitLoss[index];
    this.ngxSmartModalService.getModal('betModal').open();
  }
}
