import { CustomHttpClient } from './api/custom-http-client';
import { BehaviorSubject } from 'rxjs';
import { Injectable } from '@angular/core';

@Injectable()
export class GlobalService {

    public commentary$ = new BehaviorSubject(null);
    public balance$ = new BehaviorSubject(null);

    constructor(private chttp: CustomHttpClient) {
        setInterval(() => {
            this.getBalance();
            this.getCommentory();
        }, 100000);
    }

    getCommentory() {
        this.chttp.post('v2/events/event/commentary', {}).subscribe(resp => {
            if (resp.status) {
                this.commentary$.next(resp.data);
            }
        });
    }

    getBalance() {
        this.chttp.post('v2/users/app-history/get-balance', {}).subscribe(resp => {
            if (resp.status) {
                this.balance$.next(resp.data);
            }
        });
    }

}