import { Injectable } from '@angular/core';
import { AuthService } from './auth/auth.service';
import { CustomHttpClient } from './api/custom-http-client';
import { Router } from '@angular/router';
import { tap } from 'rxjs/operators';

@Injectable()
export class UserService {
	constructor(
		private chttp: CustomHttpClient,
		private authService: AuthService,
		private router: Router
	) {
		// if (this.authService.getStoredUser()) {
		//     this.router.navigate(['/dashboard']);
		// }
	}

	login = data => {
		return this.chttp.post('v2/app-auth/login', data, true).pipe(
			tap((res: any) => {
				if (res.status) {
					this.authService.storeUser(res.data);
				}
			})
		);
	};

	logout = () => {
		return this.chttp
			.post('v2/app-auth/logout', {}, true)
			.pipe(
				tap((res: any) => {
					if (res.status === 'success') {
						this.authService.removeUser();
						this.router.navigate(['mobile/login']);
					}
				})
			)
			.subscribe();
	};

	getProfile = () => {
		return this.chttp.post('v2/users/setting/bet-config', {});
	}

	changePassword = (data) => {
		return this.chttp.post('v2/users/change-password', data);
	}

	getAccountStatement = () => {
		return this.chttp.post('v2/users/app-history/transaction', {});
	}

	getBetHistory = () => {
		return this.chttp.post('v2/users/app-history/bet-history', {});
	}

	getProfitLoss = () => {
		return this.chttp.post('v2/users/app-history/profit-loss', {});
	}

	getResults = () => {
		return this.chttp.post('v2/users/app-history/market-result', {});
	}

	addRemoveFavorite(data) {
		// {
		// 	"event_id": "abc",
		// 	"market_id": "abc",
		// 	"market_type": "match_odd|match_odd2|fancy|fancy2",
		// 	"favorite": "add|remove"
		// }
		return this.chttp.post('v2/users/setting/add-remove-favorite', data);
	}
}
