import { Injectable } from '@angular/core';
import { CustomHttpClient } from './api/custom-http-client';

@Injectable()
export class SportsService {
    constructor(private chttp: CustomHttpClient) {

    }

    getSports() {
        return this.chttp.post('v2/events/app-event/list', {}, true);
    }

    getSportsEvents(slug) {
        return this.chttp.post(`v2/events/app-${slug}`, {}, true);
    }
}